@mixin breakpoint ($dev) {
    @if $dev==tablet{
        @media (min-width:768px){
            @content;
        }
    }
    @else if $dev==laptop{
        @media (min-width:1024px){
            @content;
        }
    }
    @else if $dev==laptopL{
        @media (min-width:1440px){
            @content;
        }
    }
}