@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
*{padding:0;margin:0;box-sizing:border-box}:root{--darkBlue:hsl(207, 26%, 17%);--veryDarkBlue:hsl(209, 23%, 22%);--lightFont:#fff;--darkFont:#000;--shadow:0 0 0}body{font-family:'Nunito Sans', sans-serif;background:hsl(209, 23%, 22%);background:var(--veryDarkBlue)}.header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding:1rem 1rem;background:hsl(207, 26%, 17%);background:var(--darkBlue);-webkit-align-items:center;align-items:center;font-size:16px;transition:0.3s}@media (min-width: 768px){.header{font-size:24px;padding:2rem}}.header__logo{font-weight:800;color:#fff;color:var(--lightFont)}.header__themeSwitch{cursor:pointer;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;display:flex;text-decoration:none;color:#fff;color:var(--lightFont)}.header__themeSwitch__moonIcon{max-width:20px;margin-right:0.6rem}.header__themeSwitch__text{min-width:100px;font-weight:800}@media (min-width: 768px){.header__themeSwitch__text{min-width:150px}}.countries{display:grid;grid-template-columns:1fr;grid-gap:3rem;gap:3rem;padding:1rem;background:hsl(209, 23%, 22%);background:var(--veryDarkBlue);transition:0.3s}@media (min-width: 768px){.countries{grid-template-columns:1fr 1fr}}@media (min-width: 1024px){.countries{grid-template-columns:repeat(3, 1fr)}}@media (min-width: 1440px){.countries{grid-template-columns:repeat(4, 1fr)}}.countries__item{box-shadow:0 0 0;box-shadow:var(--shadow);color:#fff;color:var(--lightFont);max-width:500px;border-radius:.5rem;background:hsl(207, 26%, 17%);background:var(--darkBlue)}.countries__item__flag{width:100%;border-radius:.5rem .5rem 0 0}.countries__item__details{margin-top:-8px;padding:2rem;border-radius:0 0 .5rem .5rem}.countries__item__details__title{margin-bottom:1rem;font-size:1.5em}.countries__item__details__info{font-size:1.2em;margin:.3rem 0}.countries__item__details__info span{opacity:.6}.form{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding:1rem;-webkit-flex-direction:column;flex-direction:column;background:hsl(209, 23%, 22%);background:var(--veryDarkBlue);font-size:18px}@media (min-width: 768px){.form{-webkit-flex-direction:row;flex-direction:row;padding:1rem 2rem;font-size:22px}}.form__input{box-shadow:0 0 0;box-shadow:var(--shadow);font-size:1em;padding:0.5rem 1rem;border:none;border-radius:0.5rem;margin-bottom:1rem;outline:none;background:hsl(207, 26%, 17%);background:var(--darkBlue);color:#fff;color:var(--lightFont);max-width:420px}@media (min-width: 768px){.form__input{margin-bottom:0;padding:0.8rem 1rem}}.form__input::-webkit-input-placeholder{-webkit-transition:0.2s;transition:0.2s}.form__input:-ms-input-placeholder{-ms-transition:0.2s;transition:0.2s}.form__input::placeholder{transition:0.2s}.form__input:focus::-webkit-input-placeholder{font-size:0.8em}.form__input:focus:-ms-input-placeholder{font-size:0.8em}.form__input:focus::placeholder{font-size:0.8em}.form__select{color:#fff;color:var(--lightFont);padding:0.5rem 1rem;border-radius:0.5rem;max-width:200px;font-size:1em;background:hsl(207, 26%, 17%);background:var(--darkBlue);outline:none;cursor:pointer}.form__select option{font-size:0.7em}@media (min-width: 768px){.form__select{max-width:280px}}.goBackBtn,.learnMoreBtn,.linkToCountry{text-decoration:none;text-align:center}.goBackBtn p,.learnMoreBtn p,.linkToCountry p{color:#fff;color:var(--lightFont);padding:0.8rem 1rem;border-radius:0.5rem;margin:1rem 0;max-width:120px;background:hsl(209, 23%, 22%);background:var(--veryDarkBlue);box-shadow:0 0 0;box-shadow:var(--shadow);transition:0.2s}.goBackBtn p:hover,.learnMoreBtn p:hover,.linkToCountry p:hover{-webkit-transform:translate(-1px, 1px);transform:translate(-1px, 1px)}.goBackBtn p{background:hsl(207, 26%, 17%);background:var(--darkBlue)}.goBackBtn p i{margin-right:0.5rem}.countryWebsite{padding:1.5rem}.countrySite{font-size:20px;color:#fff;color:var(--lightFont)}@media (min-width: 768px){.countrySite{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;text-align:center;-webkit-justify-content:center;justify-content:center}}@media (min-width: 1440px){.countrySite{-webkit-flex-direction:row;flex-direction:row;font-size:24px}}.countrySite img{width:100%;max-width:600px}@media (min-width: 1440px){.countrySite__textWrap{margin-left:4rem}}.countrySite__textWrap h3{font-size:1.4em;margin:1rem 0}.countrySite__textWrap p{margin:0.2rem 0}.countrySite__textWrap p span{opacity:0.60}.countrySite__textWrap__subInfo{margin-top:2rem}.countrySite__textWrap__borders{margin-top:2rem;max-width:800px}.countrySite__textWrap__borders p{margin-bottom:1rem;min-width:200px}.countrySite__textWrap__borders__borderCountries{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.countrySite__textWrap__borders__borderCountries .linkToCountry{min-width:90px}.countrySite__textWrap__borders__borderCountries .linkToCountry p{margin:.2rem .5rem;background:hsl(207, 26%, 17%);background:var(--darkBlue)}.lightThemeShadow{background:#fff;color:#000;box-shadow:1px 1px 5px #555}.lightThemeShadow p{color:#000}.lightThemeShadow h3{color:#000}.lightThemeShadow .fas{color:#000}.lightTheme{background:#fff;color:#000}.lightTheme p{color:#000}.lightTheme h3{color:#000}.lightTheme .fas{color:#000}

